// app/dashboard/admin/sent/page.tsx
import React from "react";

export const metadata = {
  title: "Sent — Admin · SignalHub",
  description: "Messages sent by your organization — logs, delivery status and quick actions.",
};

export default function AdminSentPage() {
  return (
    <main className="p-6">
      <div className="max-w-6xl mx-auto">
        <header className="mb-6">
          <h1 className="text-2xl font-semibold">Sent</h1>
          <p className="text-sm text-slate-600 mt-1">Recent outbound messages and their delivery status.</p>
        </header>

        <section className="rounded-xl border p-4 bg-white/70">
          <p className="text-sm text-slate-600">
            Server-rendered history is good for SEO — lazy-load heavy client widgets if needed.
          </p>

          <div className="mt-4 grid gap-3">
            <div className="p-3 rounded-md border bg-white">Sent message placeholder — status: delivered.</div>
            <div className="p-3 rounded-md border bg-white">Sent message placeholder — status: failed.</div>
          </div>
        </section>
      </div>
    </main>
  );
}
